.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .section {
    margin-bottom: 20px;
  }
  
  .title {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .message {
    color: #333;
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .feedback-buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }
  
  .feedback-button {
    margin-right: 10px;
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #333;
    border: none;
    cursor: pointer;
  }
  
  .statistics {
    background-color: #f5f5f5;
    padding: 20px;
  }
  
  .statistics-item {
    margin-bottom: 10px;
  }
  
  .statistics-label {
    font-weight: bold;
  }
  
  .statistics-value {
    margin-left: 10px;
  }